*{
  background:#023047;
}
a{
  text-decoration:none;
  color:#fff;
  font-size:20px;
  margin:0 20px;
  font-weight:bold;
}
button{
  outline:none;
  border:none;
  font-size:20px;
  color:#fff;
  font-weight:bold;
}
nav{
  display:flex;
  align-items:center;
  justify-content:space-evenly;
}
nav .logo a{
  display:flex;
  justify-content:space-between;
  align-items:center;
  font-size:25px;
}
nav .logo h1{
  margin:0px 20px
}
nav .navbar ul{
  list-style:none;
}
nav .navbar ul{
  display:flex;

}
nav .navbar ul li a:hover{
  color:#fca311;
  transition:0.3s ease-in-out;

}
nav .otherItems .lang button{
    background:#0a9396;
    border-radius:40px;
    padding:10px;
}

